<template>
  <div class="home">
    <div class="head_bg"></div>
    <div class="charts">
      <div class="font_title">援鄂地图</div>
      <div class="charts_div" id="map"></div>
    </div>
    <div class="Tabs">
      <router-link class="TabsFont" to="rescue">
        <i class="rescueIcon iconfont iconjiuyuan"></i>&nbsp; 救援队
      </router-link>
      <router-link class="TabsFont" to="people">
        <i class="peopleIcon iconfont iconfujin"></i>
        身边的逆行者
      </router-link>
    </div>
    <router-view />
    <div class="footer">
      <span class="share_button" @click="open">
        <i class="iconfont iconzhuanfa1"></i>
        转发分享
      </span>
    </div>
    <DialogPosters :state="status" @transfer="closes"></DialogPosters>
  </div>
</template>
<script>
import wxShare from "@/mixins/wechat";
import { getList } from "@/api/map_api";
import DialogPosters from "@/components/dialogPosters";
export default {
  data() {
    return {
      mapOption: {},
      status: false,
      chinaDatas: []
    };
  },
  mixins: [wxShare],
  components: {
    DialogPosters
  },
  created() {
    this.$nextTick(() => {
      this.getData();
    });
  },
  mounted() {
    this.setShare()
  },
  methods: {
    setShare() {
      let root='http://'+location.host+'/';
      console.log(window.location.href)
      let res = window.location.href.replace(window.location.href.split("#")[0].split('?')[1],'');
      let link = res.replace('?','');
      console.log(link)
      const shareInfo = {
        title: `致敬逆行者`,
        desc: `有一种疾病比病毒更可怕，那就是“健忘”！请记住这些英雄！`,
        link: link,
        img: root + "static/images/0220.jpg"
      };
      this.wechatShare(shareInfo);
    },
    open() {
        this.status = true;
        document.body.style.height = "100%";
        document.body.style.overflow = "hidden";
    },
    closes(state) {
      this.status = false;
      document.body.style.height = "100%";
      document.body.style.overflow = "visible";
    },
    getData() {
      let _this = this;
      let list = [];
      getList().then(res => {
        for (let index = 0; index < res.data.length; index++) {
          list.push([res.data[index]]);
        }
        _this.chinaDatas = list;
        this.init();
      });
    },
    init() {
      // console.log(this.chinaDatas);
      var echarts = require("echarts");
      // 基于准备好的dom，初始化ECharts实例
      let myChart = echarts.init(document.getElementById("map"));
      var chinaGeoCoordMap = {
        黑龙江: [127.9688, 45.368],
        内蒙古: [110.3467, 41.4899],
        吉林: [125.8154, 44.2584],
        北京: [116.4551, 40.2539],
        辽宁: [123.1238, 42.1216],
        河北: [114.4995, 38.1006],
        天津: [117.4219, 39.4189],
        山西: [112.3352, 37.9413],
        陕西: [109.1162, 34.2004],
        甘肃: [103.5901, 36.3043],
        宁夏: [106.3586, 38.1775],
        青海: [101.4038, 36.8207],
        新疆: [87.9236, 43.5883],
        西藏: [91.11, 29.97],
        四川: [103.9526, 30.7617],
        重庆: [108.384366, 30.439702],
        山东: [117.1582, 36.8701],
        河南: [113.4668, 34.6234],
        江苏: [118.8062, 31.9208],
        安徽: [117.29, 32.0581],
        湖北: [114.3896, 30.6628],
        浙江: [119.5313, 29.8773],
        福建: [119.4543, 25.9222],
        江西: [116.0046, 28.6633],
        湖南: [113.0823, 28.2568],
        贵州: [106.6992, 26.7682],
        云南: [102.9199, 25.4663],
        广东: [113.12244, 23.009505],
        广西: [108.479, 23.1152],
        海南: [110.3893, 19.8516],
        上海: [121.4648, 31.2891]
      };

      var convertData = function(data) {
        var res = [];
        for (var i = 0; i < data.length; i++) {
          var dataItem = data[i];
          var fromCoord = chinaGeoCoordMap[dataItem[0].province];
          var toCoord = [114.3896, 30.6628];
          if (fromCoord && toCoord) {
            res.push([
              {
                coord: fromCoord,
                value: dataItem[0].num
              },
              {
                coord: toCoord
              }
            ]);
          }
        }
        return res;
      };
      var series = [];
      [["湖北", this.chinaDatas]].forEach(function(item, i) {
        // console.log(item);
        series.push(
          {
            type: "lines",
            zlevel: 2,
            effect: {
              show: true,
              period: 4, //箭头指向速度，值越小速度越快
              trailLength: 0.8, //特效尾迹长度[0,1]值越大，尾迹越长重
              symbol: "roundRect", //箭头图标
              symbolSize: 3 //图标大小
            },
            lineStyle: {
              normal: {
                width: 1, //尾迹线条宽度
                opacity: 0.8, //尾迹线条透明度
                curveness: 0.2, //尾迹线条曲直度
                color: "#efec00"
              }
            },
            data: convertData(item[1])
          },
          {
            type: "effectScatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              //涟漪特效
              period: 4, //动画时间，值越小速度越快
              brushType: "stroke", //波纹绘制方式 stroke, fill
              scale: 1 //波纹圆环最大限制，值越大波纹越大
            },
            label: {
              normal: {
                show: false,
                position: "right", //显示位置
                offset: [5, 0], //偏移设置
                formatter: function(params) {
                  //圆环显示文字
                  return params.data.name;
                },
                fontSize: 12
              },
              emphasis: {
                show: false
              }
            },
            symbol: "circle",
            symbolSize: function(val) {
              return 5; //圆环大小
            },
            itemStyle: {
              normal: {
                show: false,
                color: "#efec00"
              }
            },
            data: item[1].map(function(dataItem) {
              // console.log(dataItem[0]);
              return {
                name: dataItem[0].province,
                value: chinaGeoCoordMap[dataItem[0].province].concat([
                  dataItem[0].num
                ])
              };
            })
          },
          //被攻击点
          {
            type: "scatter",
            coordinateSystem: "geo",
            zlevel: 2,
            label: {
              normal: {
                show: false,
                position: "right",
                //offset:[5, 0],
                color: "#0f0",
                formatter: "{b}",
                textStyle: {
                  color: "#fff"
                }
              },
              emphasis: {
                show: false,
                color: "#fff"
              }
            },
            symbol: "circle",
            color: "#FFC252",
            symbolSize: 7,
            data: [
              {
                name: item[0],
                value: chinaGeoCoordMap[item[0]].concat([10])
              }
            ]
          }
        );
      });

      let option = {
        tooltip: {
          trigger: "item",
          formatter: function(params, ticket, callback) {
            //根据业务自己拓展要显示的内容
            var res = "";
            var name = params.name;
            if (name == "") {
              return "";
            }
            var value = params.value[params.seriesIndex + 1];
            res =
              "<span style='color:#fff;'>" +
              name +
              "支援医疗队</span><br/>" +
              name +
              "：" +
              value +
              "人";
            return res;
          }
        },
        backgroundColor: "rgba(0, 0, 0, 0.0)",
        // visualMap: {
        //   //图例值控制
        //   min: 0,
        //   max: 1,
        //   calculable: true,
        //   show: false,
        //   color: ["#FFC252", "#fc9700", "#ffde00", "#ffde00", "#efec00"],
        //   textStyle: {
        //     color: "#fff"
        //   }
        // },
        geo: {
          map: "china",
          zoom: 1.2,
          label: {
            normal: {
              show: true,
              fontSize: 8,
              color: "#fff"
            },
            emphasis: {
              show: true,
              color: "#fff"
            }
          },
          roam: false, //是否允许缩放
          itemStyle: {
            normal: {
              color: "rgba(51, 69, 89, .5)", //地图背景色
              borderColor: "#516a89", //省市边界线00fcff 516a89
              borderWidth: 1
            },
            emphasis: {
              color: "rgba(37, 43, 61, .5)" //悬浮背景
            }
          }
        },
        series: series
      };
      myChart.setOption(option, true);
    }
  }
};
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
  .head_bg {
    width: 100%;
    height: 160px;
    background: url("../../../static/images/bannercopy.jpg") no-repeat;
    background-size: 100% 100%;
  }
  .charts {
    width: 100%;
    height: 250px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    .font_title {
      width: 100%;
      height: 35px;
      line-height: 38px;
      padding: 0 8px;
      color: #67a0fb;
    }
    .charts_div {
      width: 100%;
      background: url("../../../static/images/bg_img.png") no-repeat;
      flex-grow: 1;
    }
  }
  .Tabs {
    margin: 15px 0 8px;
    width: 100%;
    height: 40px;
    background-color: #fff;
    display: flex;
    .TabsFont {
      width: 50%;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      color: #000;
      .rescueIcon {
        font-size: 17px;
      }
      .peopleIcon {
        font-size: 24px;
      }
    }
    .TabsFont:hover {
      color: #67a0fb;
    }
    a {
      text-decoration: none;
      -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
      -webkit-user-select: none;
      -moz-user-focus: none;
      -moz-user-select: none;
    }
    .router-link-active {
      text-decoration: none;
    }
  }
  .footer {
    position: fixed;
    z-index: 10;
    bottom: 0;
    width: 100%;
    height: 40px;
    // background-color: #f8f8f8;
    background-color: rgba(255, 255, 255, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    .share_button {
      padding: 5px 15px;
      border-radius: 25px;
      font-size: 14px;
      background-color: #95bdfc;
      color: #fff;
      i {
        font-size: 14px;
        color: #fff;
      }
    }
  }
}
</style>



